@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.infoBox{
    flex: 1 1;
    cursor: pointer;
}
.infoBox:not(:last-child){
    margin-right: 15px;
}
.infoBox__cases{
    color: #e56b6f;
}
.infoBox--selected{
    border-top: 10px solid #0081a7;
}
.map{
    height: 550px;
    background-color: rgb(255, 255, 255);
    padding: 1rem;
    border-radius: 10px;
    margin-top: 16px;
    box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.507);
}
.map .leaflet-container{
    height: 100%;
}
.info-flag img{
    width: 80px;
    border-radius: 5px;
}
.info-flag{
    height: 90px;
    margin-bottom: 4px;
    width: 100%;
    background-size: cover;
    border-radius: 8px;
}
.info-container{
    width: 150px;
}
.info-name{
    font-size: 20px;
    font-weight: bold;
    color: rgb(116, 116, 116);
}
.table{
    margin-top: 20px;
    overflow: scroll;
    height: 400px;
    color: rgb(51, 50, 50);
}
.table tr{
    display: flex;
    justify-content: space-between;
}
.table td{
    padding: 0.5rem;
}
.table tr:nth-of-type(odd){
    color: rgb(65, 65, 65);
    background: rgba(74, 112, 134, 0.089);
}
*{
  margin: 0;
}
body{
  background: rgba(128, 128, 128, 0.048);
}
.app{
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
}

.app__header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}
.app__header > h1{
  font-family: 'Montserrat', sans-serif;
  color: #0081a7;
  font-size: 25px;
}
.header_names{
  font-family: 'Montserrat', sans-serif;
  background: #0081a7;
  color: #ffffff;
  font-size: 20px;
  padding: 5px 10px;
  border-radius: 5px;
}
.app__stats{
  margin: 2.5rem 0;
  display: flex;
  justify-content: space-between;
}
.header__span{
  font-family: 'Montserrat', sans-serif;
  color: #ffffff;
  padding: 2px 5px;
  border-radius: 5px;
  font-size: 25px;
  background: #0081a7;
}
.app__right{
  display: flex;;
  flex-direction: column;
  text-align: center;
}
.app__right .MuiCardContent-root{
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.app__left{

  flex-grow: 0.7;
  /* display: flex;
  flex-direction: column;
  justify-content: space-evenly; */
  height: 100%;
}
.LineGraph{
  margin: 2rem 0;
}
.Table{
  margin: 1rem 0;
}

/* -------------- Media ---------------- */
@media (max-width:990px){
  .app{
    flex-direction: column;
  }
}
